import request from "@/utils/request.js"
import router from '../router'
import {Toast} from 'vant'
export default {
    state:()=>(
    {
      username:"",
      avatar:"",
      isLogin: false,
      token: ''
    })
    //module中的state也得写函数
    ,
    mutations: {
      login (state, { username, token }) {
        state.username = username
        state.avatar = "https://img.yzcdn.cn/vant/cat.jpeg"
        state.token = token
        state.isLogin = true
        sessionStorage.setItem("token", token);
        router.push('/')
      },
      regist (state, { username, token }) {
        state.username=username,
        state.avatar="https://img.yzcdn.cn/vant/cat.jpeg"
        state.token = token
        state.isLogin = true
        localStorage.setItem("token", token);
        router.push('/')
      },
      logout (state) {
        state.user = {}
        state.token = ''
        state.avatar=""
        state.isLogin = false
        sessionStorage.setItem("token", "");
        router.push('/')
      }
    },
    actions: {
      registAsync (store, values) {
        // 模拟，实际应该发送 ajax 请求
        Toast.loading({
          duration:0
        })
        request.post("/api/UserRegister",values).then(resp => {
            store.commit('regist', {
              username: values.username,
              token:resp.token
              // token: 'asdlfkjasdoijlkjasiodfjqljewrlkasdjfoasdjflkasj'
            })
            Toast("注册成功")
            Toast.clear()
        }
     )
      },  
      loginAsync (store, { username, password }) {
        Toast.loading({
            duration:0
          })
            // 模拟，实际应该发送 ajax 请求
        request.post("/api/UserLogin",{username,password}).then(resp => {
                store.commit('login', {
                  username: username,
                  // token: 'asdlfkjasdoijlkjasiodfjqljewrlkasdjfoasdjflkasj',
                  token:resp.token
                })
                Toast("登陆成功")
                Toast.clear()
            }
        )
      },
      logoutAsync (store, { username, password }) {
        Toast.loading({
            duration:0
          })
        request.get("/api/UserLogout").then(resp => {
              store.commit('logout')
              Toast("退出成功")
              Toast.clear()
            }
        )
      }
 }
}
  