<template>
  <div class="login">
    <van-form @submit="onSubmit">
      <van-field
        v-model="username"
        name="username"
        label="用户名"
        placeholder="用户名"
        :rules="[{ required: true, message: '请填写用户名' }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="password"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
      />
      <van-field
        v-model="phone"
        type="number"
        name="phone"
        label="手机号"
        placeholder="手机号"
        :rules="[{ required: true, message: '请填写手机号' }]"
      />
      <van-field
        v-model="email"
        type="email"
        name="email"
        label="邮箱"
        placeholder="邮箱"
        :rules="[{ required: true, message: '请填写邮箱' }]"
      />
      <!-- <van-radio-group  style="display:flex" v-model="gender">
        <van-radio name="male">♂</van-radio>
        <van-radio name="female">♀</van-radio>
     </van-radio-group> -->
      <div style="margin: 16px;">
        <van-button round block type="info" native-type="submit">
          注册
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import Vue from 'vue'
import { Form, Field, Button,RadioGroup, Radio  } from 'vant'
import { mapActions } from 'vuex'

Vue.use(Form)
Vue.use(Field)
Vue.use(Button)
Vue.use(Radio);
Vue.use(RadioGroup);
export default {
  name: 'Register',
  data () {
    return {
      username: 'admin',
      password: 'admin',
      email:'admin@foo.com',
      phone:'123456',
      gender:'male'
    }
  },
  created () {
    console.log(this.$store)
  },
  methods: {
    ...mapActions(['loginAsync','registAsync']),
    onSubmit (values) {
      // 触发 vuex 在 store 中对用户状态管理中 action 方法的调用（异步）
      // this.$store.dispatch('loginAsync', values)
      // 这种写法调用到了 mapActions 中的 loginAsync 方法
      this.registAsync(values)
    }
  }
}
</script>

