<template>
  <div class="login">
    <div class="images">
	    <img src="https://s1.hdslb.com/bfs/static/passport/static/img/rl_top.35edfde.png" alt="">
  	</div>	
    <van-form @submit="onSubmit">
      <van-field
        v-model="username"
        name="username"
        label="用户名"
        placeholder="用户名"
        :rules="[{ required: true, message: '请填写用户名' }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="password"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
      />
      <div>
		    <van-checkbox v-model="checked">记住我</van-checkbox>
	    </div>
      <div style="margin: 16px;">
        <van-button round block type="info" native-type="submit">
          登录
        </van-button>
      </div>
    </van-form>
    <p class="text">未注册或未绑定的手机号，将帮你注册新账号，登录即代表你同意
	      <router-link to="/agreement">用户协议</router-link>
	  </p>
  </div>
</template>

<script>
import Vue from 'vue'
import { Form, Field, Button,Checkbox,CheckboxGroup} from 'vant'
import { mapActions } from 'vuex'
Vue.use(Checkbox);
Vue.use(CheckboxGroup);
Vue.use(Form)
Vue.use(Field)
Vue.use(Button)

export default {
  name: 'Login',
  data () {
    return {
      username: 'admin',
      password: 'admin',
      checked:"checked"
    }
  },
  created () {
  },
  methods: {
    ...mapActions(['loginAsync']),
    onSubmit (values) {
      // 触发 vuex 在 store 中对用户状态管理中 action 方法的调用（异步）
      // this.$store.dispatch('loginAsync', values)
      // 这种写法调用到了 mapActions 中的 loginAsync 方法
      this.loginAsync(values)
    }
  }
}
</script>
<style scoped lang="scss">
	.van-form{
		margin-top: -5px;
		.van-checkbox{
			margin: 20px;
		}		
	}
	.login{
		display: inline-block;
		padding: 15px 0 30px;
	
	}
	.images{
		width: 100%;
		height: 150px;
		img{
			width: 100%;
			height: 100%;
		}
	}
	.text{
		color: #CCCCCC;
		font-size: 14px;
		padding: 8px 20px;
		line-height: 27px;
		a{
			color: red;
		}
	}
</style>