<template>
<div class="top">
      <router-link to="/userhome/1">
      <van-image v-if="user.avatar"
        width="2rem"
        height="2rem"
        round
        :src="user.avatar"
        alt="请先登录"
      />
      </router-link>
      <van-search style="width:18rem" required v-model="searchWords" @search="onSearch"  placeholder="请输入搜索关键词" />
      <van-icon v-if="user.avatar" name="setting-o" size="1.5rem" to="/activity" @click="goSetting" />
    </div>
</template>

<script>
import Vue from "vue"
import {Search} from 'vant'
Vue.use(Search)
export default {
  name: 'searchHeader',
  data () {
     return {
      searchWords:"",
      avatar:'/assets/logo.png'
    };
  },
  computed:{
    user(){
      return {
        username:this.$store.state.user.username,
        avatar:this.$store.state.user.avatar,
      }
    }
  },
  methods: {
  onSearch(val) {
      this.$router.push("/search/"+val)
    },
  onCancel() {
      Toast('取消');
    },
  goSetting(){
      this.$router.push("/setting")
  }
  }
}
</script>

<style scoped lang=scss>
  .top{
    display: flex;
    .van-image{
      padding: 0.6rem 0.3rem;
      cursor:pointer
    }
    .van-icon{
      padding: 0.8rem 0.3rem 0.6rem 0.2rem;
      cursor:pointer
    }
  }
</style>