<template>
 <div>
  <van-tabs>
      <van-tab title="我的视频" name="a">
         <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          @load="myonLoad"
          class="container"
    >
    <van-cell v-for="item in mylist" :key="item.id" class="video">
             <router-link :to="'/video/vid='+item.id">
                <img :src="item.src" alt="">
                {{item.title}}
             </router-link>
    </van-cell>
         </van-list>    
      </van-tab>
      <van-tab title="我的收藏" name="b">
         <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="favonLoad"
      class="container"
    >
    <van-cell v-for="item in favlist" :key="item.id" class="video">
             <router-link to="/video/vid=1">
                <img :src="item.src" alt="">
                {{item.title}}
             </router-link>
    </van-cell>
         </van-list>    
      </van-tab>
  </van-tabs>
 </div>
</template>

<script>
import request from "@/utils/request.js"
import Vue from 'vue';
import { Icon } from 'vant';
Vue.use(Icon)
export default {
  name: '',
  data() {
    return {
        seq:this.$route.params.id,
        favlist:[],
        mylist:[],
        swipes:[],
        loading:false,
        finished:false,
        mypage:1,
        favpage:1
    };
  },
  methods: {
      myonLoad() {
      request(`/api/tab/?seq=${this.seq},start=${this.page}`).then(resp=>{
                  this.mylist = this.mylist.concat(resp.videoList)
                  this.page++
                  this.loading = false 
              })   
     },
     favonLoad() {
      request(`/api/tab/?seq=${this.seq},start=${this.page}`).then(resp=>{
                  this.favlist = this.favlist.concat(resp.videoList)
                  this.page++
                  this.loading = false 
              })   
     },

  },
  watch:{
      $route:{
          handler:function (newRoute) {
              this.seq = newRoute.params.seq
              request(`/api/tab/?seq=${this.seq},start=${this.page}`).then(resp=>{
                  this.favList = resp.videoList
                  this.myList = resp.videoList
              })   
            },
            immediate:true
      }
  }
}
</script>

<style scoped lang=scss>
.container{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  .video{
        margin:10px 0px ;
        flex-direction: column;
        width: 45%;
        display: flex;
        img{
          width: 100%;
          height:5rem;
        }
      }
  }


</style>