import Vue from 'vue'
import Vuex from 'vuex'
import userModule from './user-module'

Vue.use(Vuex)

// 创建 store：store 用户存储数据及数据更新的动作
const store = new Vuex.Store({
  strict: true, // 严格模式：限定状态的修改是在 mutation 中进行，如果在非 mutation 方法中修改状态数据，则报错
  modules: { // 模块
    user: userModule
  },
  state: { 
  },
  getters: {},
  mutations: { // mutation, 同步更新 state 时使用的方法
  },
  actions: {} // 通常异步更新 state 时的方法
})

export default store
