/* axios 二次封装 */
import axios from "axios";
// 判断开发环境与生产环境
const baseURL = process.env.NODE_ENV !== 'development'
  ? 'http://rap2api.taobao.org/app/mock/266140'
  : 'localhost:8080'

// 创建 axios 实例
const service = axios.create({
  baseURL,
});



let token = '';

service.defaults.withCredentials = false;
service.defaults.headers.common['token'] = token;
service.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';//配置请求头

//添加一个请求拦截器
service.interceptors.request.use(
  config => {
    const token = sessionStorage.getItem("token");
    if (token) {
      config.headers.common['Authorization'] = token;
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

// 响应拦截
service.interceptors.response.use((response) => {
  // response 中包装了从服务器响应回来的数据
  console.log("响应拦截：", response);
  // Toast(response.data.message)
  if (response.status >= 200 && response.status < 300) {
    // 成功
    if (response.data.code == 200) {
      // 前后端规范中 code 为 200 表示数据正常
      return response.data.data; // 返回正确的数据内容
    }
  }
  // 有异常时返回的数据内容
  return Promise.reject(response);
});

// 导出 axios 实例
export default service;
