import Login from "@/views/Login.vue";
import Home from "@/views/Home/index.vue";
import Register from "@/views/Register.vue";
import SubCategory from "@/views/Home/SubCategory.vue";
import Four from "@/views/404.vue";
import Activity from "@/views/activity.vue";
import UserHome from "@/views/userhome.vue";
import Search from "@/views/search.vue";
import Video from "@/views/video.vue"
import Setting from "@/views/setting.vue"
import Upload from "@/views/upload.vue"
const routes = [
  {
    path:'/',
    redirect:'/home/sub/1'
  },
  {
    path: "/home",
    redirect:'/home/sub/1',
    component: Home,
    children:[
      {
        path:"sub/:seq",
        component: SubCategory
      }
    ],
    meta: { // 路由元信息：携带的额外数据，可以在开发时使用到
      isTabBar: true,
      title: '首页',
      icon: 'home',
    }
  },
    {
    path: "/login",
    component: Login,
    meta: { // 路由元信息：携带的额外数据，可以在开发时使用到
      isTabBar:true ,
      title: '登录',
      icon: 'user',
      status:'out',
      isLogin: false
    }
  },
  {
    path: "/register",
    component: Register,
    meta: { // 路由元信息：携带的额外数据，可以在开发时使用到
      isTabBar: true,
      title: '注册',
      icon: 'user',
      status:'out',
      isLogin: false
    }
  },
  {
    path: "/activity",
    component: Activity,
    meta: { // 路由元信息：携带的额外数据，可以在开发时使用到
      // isTabBar: true,
      title: '动态',
      icon: 'listblock',
      status:'in',
      isLogin: true
    }
  },
  {
    path: "/setting",
    component: Setting,
    meta: { // 路由元信息：携带的额外数据，可以在开发时使用到
      title: '设置',
      isLogin: true
    }
  },
  {
    path: "/upload",
    component: Upload,
    meta: { // 路由元信息：携带的额外数据，可以在开发时使用到
      title: '上传',
      isLogin: true,
      isTabBar:true,
      icon: 'listblock',
    }
  },
  {
    path:"/404",
    component:Four
  },
  {
    path:"/userhome/:id",
    component:UserHome,
    meta: { // 路由元信息：携带的额外数据，可以在开发时使用到
      title: '我的',
      icon: 'user',
      isTabBar: true,
      isLogin: true
    }
  },
  {
    path:"/search/:words",
    component:Search,
    meta:{
      title:"搜索"
    }
  },
  {
    path:"/video/vid=:vid",
    component:Video,
    meta: { // 路由元信息：携带的额外数据，可以在开发时使用到
      title: '视频',
      icon: 'listblock'
    }
  }
];
export default routes;
