<template>
  <div>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
      class="container"
    >
    <van-cell v-for="item in list" :key="item.id" class="video">
             <router-link :to="'/video/vid='+item.id">
                <img :src="item.cover" alt="">
                {{item.videocaption}}
             </router-link>
    </van-cell>
    </van-list>    
  </div>
</template>

<script>
import request from "@/utils/request.js"
import Vue from "vue"
export default {
  name: "",
  data() {
    return {
        seq:this.$route.params.words,
        list:[],
        swipes:[],
        loading:false,
        finished:false,
        page:1
    };
  },
  methods: {
    onLoad() {
      request(`/api/SearchVideo/?words=${this.words},start=${this.page}`).then(resp=>{
                  this.list = this.list.concat(resp.videoList)
                  this.page++
                  this.loading = false 
              })   
     }
  },
  watch:{
      $route:{
          handler:function (newRoute) {
              this.words = newRoute.params.words
              request(`/api/SearchVideo/?words=${this.words},start=0`).then(resp=>{
                  this.list = resp.videoList
                  this.loading = false 
              })   
            },
            immediate:true
      }
  }
};
</script>

<style scoped lang=scss>
.my-swipe .van-swipe-item {
    color: #fff;
    font-size: 20px;
    line-height: 150px;
    text-align: center;
    background-color: #39a9ed;
  }
.container{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  .video{
        margin:10px 0px ;
        flex-direction: column;
        width: 45%;
        display: flex;
        img{
          width: 100%;
          height:5rem;
        }
      }
  }


</style>