<template>
 <div>
     <div class="container">
        <video preload='auto' id='video'
            src='@/assets/1.mp4'
            webkit-playsinline='true'
            playsinline='true'
            x-webkit-airplay='true'
            x5-video-player-type='h5'
            x5-video-player-fullscreen='true'
            x5-video-ignore-metadata='true' 
            width='100%' height='100%'
            @click="player"
            ref = "video"
            ><p> 不支持video</p>
        </video>
     </div>
     <comment></comment>
  </div>
</template>

<script>
import Vue from "vue"
import Comment from '@/components/comment'
export default {
    data(){
        return {
            videoUrl:'',
            video:null,
        }
    },
    mounted: function(){
        this.videoUrl = "http://gamaru.wpgcms.com/media/file/movie/dkSdfdqur5uQKV5p.mp4";
        this.video = this.$refs.video;
    },
    components:{
        Comment
    },
    methods: {
        player:function(){
            if(this.video.paused){
                // 播放
                this.video.play();
            }else{
                // 暂停
                this.video.pause()
            };            
        }
    }
}
</script>

<style scoped lang=scss>
video{
    width: 100%;
}
</style>