<template>
  <div>
    <van-swipe class="my-swipe" :autoplay="2000" indicator-color="white">
      <van-swipe-item v-for="item in swipes" :key="item.id">
        <router-link to="/video/vid=1" style="color:white">
          <van-image :src="item.src" fit="contain" to='/video' >
            <template #default>
              {{item.title}}
            </template>
          </van-image>
        </router-link>
      </van-swipe-item>
    </van-swipe>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
      class="container"
    >
    <van-cell v-for="item in list" :key="item.id" class="video">
             <router-link :to="'/video/vid='+item.id">
                <img :src="item.src" alt="">
                {{item.title}}
             </router-link>
    </van-cell>
    </van-list>    
    
  </div>
</template>

<script>
import request from "@/utils/request.js"
import Vue from "vue"
import {Swipe,SwipeItem,List } from 'vant'
Vue.use(Swipe)
Vue.use(SwipeItem )
Vue.use(List)
export default {
  name: "",
  data() {
    return {
        seq:this.$route.params.id,
        list:[],
        swipes:[],
        loading:false,
        finished:false,
        page:1
    };
  },
  methods: {
     onLoad() {
      request(`/api/tab/?seq=${this.seq},start=${this.page}`).then(resp=>{
                  this.list = this.list.concat(resp.videoList)
                  this.page++
                  this.loading = false 
              })   
     }
  },
  watch:{
      $route:{
          handler:function (newRoute) {
              this.seq = newRoute.params.seq
              request(`/api/tab/?seq=${this.seq},start=0`).then(resp=>{
                  this.list = resp.videoList
                  this.swipes = resp.categories
              })   
            },
            immediate:true
      }
  }
};
</script>

<style scoped lang=scss>
.my-swipe .van-swipe-item {
    color: #fff;
    font-size: 20px;
    line-height: 150px;
    text-align: center;
    background-color: #39a9ed;
    height:7rem
  }
.container{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  .video{
        margin:10px 0px ;
        flex-direction: column;
        width: 45%;
        overflow:hidden;
        display: flex;
        img{
          width: 100%;
          height:5rem;
        }
      }
  }


</style>