<template>
 <div>
     <van-form @submit="onSubmit">
         <van-cell-group>
            <van-field v-model="title" name="title" label="标题" placeholder="请输入标题"
            :rules="[{ required: true, message: '请填写标题' }]" />
            <van-field v-model="description" name="description" label="描述" placeholder="请输入视频描述" :rules="[{ required: true, message: '请填写描述' }]"  />
         </van-cell-group>
         <van-uploader  multiple :max-count="1"  v-model="fileList" :after-read="afterRead" accept="video/*" />
        <div style="margin: 16px;">
        <van-button round block type="info" native-type="submit">
          提交
        </van-button>
      </div>
     </van-form>
  </div>
</template>

<script>
import Vue from 'vue';
import { Uploader,Toast } from 'vant';
import { Field } from 'vant';
import uploader from '@/utils/uploader.js'
import request from '@/utils/request.js'
Vue.use(Field);
Vue.use(Uploader)
Vue.use(Toast)
Toast.setDefaultOptions({ duration: 500 });
export default {
  data() {
    return {
      fileList: [
      ],
      title:'123',
      description:'123'
    };
  },
  methods: {
    async afterRead(file) {
      file.status = 'uploading';
      file.message = '上传中...';
      // let uploadImg = await uploader(file.file)//使用上传的方法。file.file
      file.status = 'done';
      file.message = '上传成功';
      console.log(this.fileList)
    //   this.$router.push('./userhome/1')
    },
    onSubmit(values) {
      if(this.fileList[0]&&this.fileList[0].status ==="done"){
        request.get('/api/upload').then(resp=>{
                    Toast("上传成功")
                    this.$router.push('/')
            })
            }
      else Toast("请上传视频") 
      }
     
  }
}
</script>

<style scoped lang=scss>
video{
    width: 100%;
}
</style>