import Vue from 'vue'
import VueRouter from 'vue-router'
import routes from './routes'
import store from '../store'
import {Toast} from 'vant'

Vue.use(VueRouter)
Vue.use(Toast)
const router = new VueRouter({
  mode: 'history',
  routes
})

// 保存需要权限的页面路由
const permissionList = ['/userhome/:id', '/activity']

// 全局前置守卫，验证登录权限
router.beforeEach((to, from, next) => {
  // to: 要跳转到的页面路由
  // from: 从哪个页面跳转
  // next：下一步（走到哪儿）
  // console.log(permissionList.includes(to.path))
  // 判断需要跳转到的页面是否需要权限
  // Toast.loading()
  if (permissionList.includes(to.path)) { // 需要用户登录
    // console.log(store.state.user.isLogin)
    // 判断用户是否已登录
    if (store.state.user.isLogin) { // 已登录
      next()
    } else { // 未登录
      Toast("请先登录")
      next('/login')
    }
  } else {
    next()
  }
})

export default router
